.fc-color-blue {
  background: -webkit-linear-gradient(45deg,#2e4e7e, #003371, #426666, #44cef6, #56004f);
  /* -moz-linear-gradient(45deg, #2e4e7e, #003371, #426666, #44cef6, #56004f);
  -ms-linear-gradient(45deg, #2e4e7e, #003371, #426666, #44cef6, #56004f); */
  color: transparent;
  -webkit-background-clip: text;
}

